* {
  margin: 0;
  padding: 0;
}
body,
html {
  position: relative;
  height: 100%;
  width: 100%;
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  font-style: normal;
}
.container {
  max-width: 1240px;
  margin-left: auto;
  margin-right: auto;
  box-sizing: border-box;
  padding-left: 50px;
  padding-right: 50px;
}
.arrow {
  text-align: center;
}
.arrow span {
  height: 85px;
  display: block;
  background-image: url("../img/arrow_long.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.bounce {
  -moz-animation: bounce 2s infinite;
  -webkit-animation: bounce 2s infinite;
  animation: bounce 2s infinite;
}
@keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-30px);
  }
  60% {
    transform: translateY(-15px);
  }
}
.header {
  position: fixed;
  top: -100px;
  left: 0;
  right: 0;
  z-index: 10;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  animation: 1s forwards;
  animation-name: slidein;
}
.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 10px;
  padding-bottom: 10px;
}
.header__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: center;
}
.header__logo {
  display: block;
  width: 50px;
  height: 50px;
  font-size: 0;
  text-decoration: none;
  background-image: url("../img/z.svg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}
.header__item {
  display: block;
  font-size: 21px;
  line-height: 50px;
  padding-right: 35px;
  text-decoration: none;
  color: black;
  margin-left: 15px;
  transition: opacity .2s;
  background-position: right top;
}
.header__item:last-child {
  padding-right: 0;
}
.header__item:hover {
  opacity: 0.8;
}
.header__item::before,
.header__item::after {
  display: inline-block;
  opacity: 0;
  -webkit-transition: -webkit-transform 0.3s, opacity 0.2s;
  -moz-transition: -moz-transform 0.3s, opacity 0.2s;
  transition: transform 0.3s, opacity 0.2s;
}
.header__item::before {
  margin-right: 10px;
  content: '[';
  -webkit-transform: translateX(20px);
  -moz-transform: translateX(20px);
  transform: translateX(20px);
}
.header__item::after {
  margin-left: 10px;
  content: ']';
  -webkit-transform: translateX(-20px);
  -moz-transform: translateX(-20px);
  transform: translateX(-20px);
}
.header__item:hover::before,
.header__item:hover::after,
.header__item:focus::before,
.header__item:focus::after {
  opacity: 1;
  -webkit-transform: translateX(0px);
  -moz-transform: translateX(0px);
  transform: translateX(0px);
}
.header__toggle {
  display: none;
  width: 65px;
  height: 25px;
  font-size: 0;
  border: 0;
  background-color: transparent;
  cursor: pointer;
  transition: opacity .2s;
  background-image: url("../img/icon__burger.svg");
}
.header__toggle:hover {
  opacity: 0.8;
}
.menu {
  display: flex;
  flex-grow: 1;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: white;
  max-height: 100%;
  height: 100%;
  width: 100%;
  z-index: 9;
  transform: translate(0, -100%);
  transition: transform 0.4s ease-in-out;
  overflow: hidden;
}
.menu_opened {
  transform: translate(0, 0);
  overflow-y: auto;
}
.menu__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-grow: 1;
}
.menu__space {
  margin-top: 20px;
  height: 100%;
  max-height: 140px;
  flex-grow: 1;
}
.menu__close {
  font-size: 0;
  text-decoration: none;
  margin-top: 11px;
  display: block;
  width: 59px;
  height: 37px;
  flex-shrink: 0;
  margin-left: auto;
  margin-right: auto;
  background-image: url("../img/icon__close.svg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}
.menu__nav {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.menu__item {
  display: inline-block;
  margin-bottom: 30px;
  padding-right: 40px;
  font-size: 35px;
  font-weight: 400;
  line-height: 35px;
  text-decoration: none;
}
.active {
  background-color: rgba(255, 255, 255, 0.9);
  box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.1), 0 3px 10px 0 rgba(0, 0, 0, 0.1);
  transition: box-shadow 0.3s;
}
.iam {
  display: flex;
  align-items: center;
  padding-top: 150px;
}
.iam__image {
  display: block;
  width: 450px;
  height: 450px;
  background-image: url("../img/me.jpeg");
  -webkit-filter: grayscale(1%);
  filter: grayscale(1%);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  margin-right: -50px;
}
.iam__image .cursor_big {
  position: relative;
  z-index: 10;
}
.iam__descr {
  position: relative;
  top: -100px;
  pointer-events: none;
  float: right;
  font-weight: 600;
  font-size: 60px;
  line-height: 80px;
  width: 800px;
  z-index: 5;
  animation: 2s slidein forwards;
}
.iam p {
  margin: 0;
  padding: 0;
}
.experience {
  font-size: 15px;
  font-weight: normal;
  line-height: 30px;
}
.experience__container {
  display: flex;
  justify-content: space-between;
  text-align: left;
  padding-top: 150px;
}
.experience__item {
  max-width: 350px;
}
.experience__title {
  margin-top: 10px;
  color: red;
}
.experience__year {
  margin-top: 15px;
  color: grey;
}
.myskills__container {
  max-width: 100%;
  padding-top: 150px;
  padding-left: 0;
  padding-right: 0;
  overflow: hidden;
}
.myskills .slideshow {
  width: 100%;
}
.myskills .slide {
  font-size: 80px;
  line-height: 105px;
  font-weight: 800;
  margin: 0 32px;
}
@supports (-webkit-text-stroke: 1px black) {
  .myskills .slide {
    -webkit-text-stroke: 1px black;
    -webkit-text-fill-color: white;
  }
}
.myprojects {
  position: relative;
  padding-top: 150px;
}
.myprojects__project {
  max-width: calc(49.2%);
  margin-bottom: 50px;
}
.myprojects__descr {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 400px;
}
.myprojects__descr .overlay {
  background: rgba(0, 0, 0, 0.5);
  color: #f1f1f1;
  width: 100%;
  transition: .5s ease;
  opacity: 0;
  color: white;
  font-size: 20px;
}
.myprojects__descr:hover .overlay {
  opacity: 1;
  transition: .5s ease;
}
.myprojects__descr span {
  font-size: 20px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  font-weight: 500;
  padding: 50px;
  box-sizing: border-box;
  height: 100%;
  text-align: center;
}
.myprojects .swiper-container {
  width: 100%;
  height: 400px;
}
.swiper-button-next,
.swiper-button-prev {
  position: absolute;
  margin-top: 50px;
  background-size: 123px;
  background-position: 5px center;
  width: 38px;
  height: 38px;
  background-color: transparent;
  border: none;
  background-image: url("../img/arrows_sprite.svg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  outline: none;
}
.swiper-button-next:hover,
.swiper-button-prev:hover {
  background-position: center;
}
.swiper-button-next:active,
.swiper-button-prev:active {
  background-position: -90px center;
}
.swiper-button-prev {
  left: -10px;
  right: auto;
  transform: rotate(180deg);
}
.swiper-button-next {
  right: -10px;
  left: auto;
}
.contact {
  padding-top: 150px;
  padding-bottom: 150px;
}
.contact__items {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.contact__item {
  text-decoration: none;
  color: black;
  font-size: 30px;
  font-weight: 600;
  line-height: 50px;
}
@keyframes slidein {
  0% {
    opacity: 0;
  }
  100% {
    top: 0;
    opacity: 1;
  }
}
.cursor-dot,
.cursor-dot-outline {
  pointer-events: none;
  position: absolute;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  opacity: 0;
  transform: translate(-50%, -50%);
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
}
.cursor-dot {
  z-index: 100;
  width: 8px;
  height: 8px;
  background-color: #FF0000;
}
.cursor-dot-outline {
  z-index: 100;
  width: 40px;
  height: 40px;
  border: solid 1px #FF0000;
}
